<template>
    
    <div>
    <form class="col-md-6">
    <table class="table">
      <thead>
        <th>購買時間</th>
        <th>Email</th>
        <!-- <th>購買款項</th> -->
        <th>應付帳款</th>
        <th>是否付款</th>
        
      </thead>
      <tbody>
        <tr v-for="item in orderlist" :key="item.id">
          <td class="align-middle">{{ item.create_at }}</td>
          <td class="align-middle">{{ item.user.email }}</td>
          <!-- <td class="align-middle">{{ item.products.product.title }}</td> -->
          <td class="align-middle">{{ item.total }}</td>
          <td class="align-middle text-right">{{ item.is_paid }}</td>
        </tr>
      </tbody>
    </table>


  </form>
    
    </div>
</template>

<script>
export default {
    data(){
        return{
          orderlist:{},
          isLoading:false
        }
    },
    methods:{
        getorderlist(){
        const vm = this;
        const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/orders?page=:page`;
        vm.isLoading = true;
        this.$http.get(api).then((response) => {
          console.log( response.data)
        vm.orderlist = response.data.orders
        // console.log(response)
        console.log(vm.orderlist)
        vm.isLoading = false;
      })
        }
    },
    created(){
        this.getorderlist();
    }
}
</script>


