import Vue from 'vue'
import Router from 'vue-router'
// import HelloWorld from '@/components/HelloWorld'
import Login from '@/components/page/login'
import Dashboard from "@/components/dashboard"
import Product from "@/components/page/product"
import CustomerOrder from "@/components/page/CustomerOrder"
import Cart from "@/components/page/cart"
import order from '@/components/page/order'
import Index from "@/components/page/index"
// import order from "@/components/page/order"
import CustomerCheck from "@/components/page/CustomerCheck"
import Orderlist from "@/components/page/orderlist"



Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '*',
      redirect:'/login'
      
    },
    {
      path: '/login',
      name: 'login',
      component: Login
    },
    

    {
      path: '/',
      name: 'Dashboard',
      component: Dashboard,
      
      children:[
        {
          path: '/',
          name: 'index',
          component: Index
        },
        {
          path: '/customerorder',
          name: 'CustomerOrder',
          component: CustomerOrder
        },
        {
          path: '/orderlist',
          name: 'Orderlist',
          component: Orderlist,
          meta: { requiresAuth: true },
        },
       

      ],
    },

    {
      path: '/admin',
      name: 'Dashboard',
      component: Dashboard,
      
      children:[
        {
          path: 'product',
          name: 'product',
          component: Product,
          meta: { requiresAuth: true },
        },
      ],
    },

    {
      path: '/cart',
      name: 'cart',
      component: Cart
    },
    {
      path: '/order',
      name: 'order',
      component: order
    },
    {
      path: '/Customercheck/:orderId',
      name: 'CustomerCheck',
      component: CustomerCheck
    },
  ]
})



