<template >
  <div>
    <meta charset="utf-8" name="viewport" content="width=device-width, initial-scale=1.0">
    <div class="slide">
      <!-- <div>
        <img src="../../assets/beer7.png" alt="" />
      </div>
      <div>
        <img src="../../assets/beer6.jpg" alt="" />
      </div>
      <div>
        <img src="../../assets/sapporo.jpg" alt="" />
      </div> -->
      <div class="ad">
        <div class="ad_text">時時刻刻與你歡聚每一刻</div>
      </div>

      <div class="product_top">
        <p>品牌介紹</p>
      </div>
      <div class="wrap">
      <div class="ad1">
        <div class="ad2" @mouseover="hover_in" @mouseout="hover_out">
          <img src="../../assets/Orion7.jpg" alt="" />
          <div class="img_content">
            <div>
              <img src="../../assets/orion.jpg" alt="" />
              <p>
                沖繩的代表性啤酒「Orion
                Beer」選用沖繩在地伊江島產大麥、稻米及山原淨水，以更長的熟成時間帶出馥郁口感！
              </p>
            </div>
          </div>
        </div>

        <div class="ad2" @mouseover="hover_in" @mouseout="hover_out">
          <img src="../../assets/Asahi8.jpg" alt="" />

          <div class="img_content">
            <div>
              <img src="../../assets/Asahi.png" alt="" />
              <p>
                Asahi Super
                Dry是款優質啤酒，令人耳目一新。乾爽清脆，味道無甜，恬淡。清澈的淺金色啤酒，泡沫細緻綿密，清甜檸檬草本香氣與清爽口感。
              </p>
            </div>
          </div>
        </div>

        <div class="ad2" @mouseover="hover_in" @mouseout="hover_out">
          <img src="../../assets/estrella_damm.png" alt="" />
          <div class="img_content">
            <div>
              <img src="../../assets/ESTRELLA.jpg" alt="" />
              <p>
                由100%純天然地中海原料釀造，麥芽、大米和啤酒花的配方使其口感清香順滑。
              </p>
            </div>
          </div>
        </div>

        <div class="ad2" @mouseover="hover_in" @mouseout="hover_out">
          <img src="../../assets/corona.jpg" alt="" />
          <div class="img_content">
            <div>
              <img src="../../assets/corona_logo.jpg" alt="" />
              <p>
                源自墨西哥的啤酒領導品牌頂級啤酒花，優質大麥，純淨水源及酵母釀製而成屬Pilsener 式窖藏啤酒，品質是其一貫的堅持。
              </p>
            </div>
          </div>
        </div>
        </div>
      </div>

      


      <div>
        <img src="../../assets/sapporo.jpg" alt="" />
      </div>
      <!-- <img src="../../assets/ad2.jpg" alt=""> -->
    </div>
  </div>
</template>

<script>
import $ from "jquery";

export default {
  data() {
    return {
      isopacity: false,
    };
  },
  methods: {
    domload() {
      $(document).ready(function () {
        $(".ad2").hover(function () {
          this.isopacity = !this.isopacity;
          // console.log( this.isopacity)
          if (this.isopacity) {
            $(".img_content").css("opacity", 1);
          } else {
            $(".img_content").css("opacity", 0.9);
          }
        });
      });
    },
   
    hover_in(e){
      $(e.path[1].children[1]).css("opacity", 1);
    },
    hover_out(e){
      $(e.path[1].children[1]).css("opacity", 0.9);
    }
  },
  created() {
    // this.domload();
  },
};
</script>



<style scoped>
@import url("https://fonts.googleapis.com/css2?family=Noto+Sans+HK:wght@700&display=swap");
.wrap{
  display: flex;
  flex-wrap: wrap;
}
.slide {
  position: absolute;
  top: 0;
  background-color: #F7f7f7;
}
img {
  width: 100%;
  /* height: 360px; */
  position: relative;
}
.ad {
  background-image: url("../../assets/admain.jpg");
  /* background-image: url(https://images.unsplash.com/photo-1546622891-02c72c1537b6?ixid=MnwxMjA3fDB8MHxzZWFyY2h8MzR8fGJlZXJ8ZW58MHx8MHx8&ixlib=rb-1.2.1&auto=format&fit=crop&w=1200&q=60); */
  background-size: cover;
  background-attachment: fixed;
  /* height: 55vh; */
  min-height: 35rem;
  background-position: 50%;
  position: relative;
}
.ad_text {
  color: white;
  font-size: 30px;
  position: absolute;
  top: 60%;
  left: 70%;
  /* background-color: black; */
  padding: 10px;
  letter-spacing:3px;
  font-weight: 600px;
}
.ad2 {
  width: 600px;
  display: flex;
  margin: 10px;
}
.ad2>img{
  width: 100%;
  height: 360px;
}
.img_content {
  width: 200px;
  height: 100%;
  background-color: white;
  opacity: 0.9;
  position: absolute;
  right: 20px;
  padding: 20px;
  /* margin-top: 20px;
  margin-bottom: 20px; */
}
.img_content p {
  font-size: 14px;
  width: 100px;
  display: inline;
  line-height: 1.8 !important;
}
.ad1 {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}
.ad1 p {
  width: 500px;
  margin: 0 auto;
  line-height: 32px;
}
.product_top {
  margin: 0 auto;
  width: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.product_top p {
  color: #014293;
  font-size: 42px;
  font-weight: 500;
  font-family: "Noto Sans HK", sans-serif;
  padding: 20px;
}
.ad2 {
  overflow: hidden;
  position: relative;
}
.ad2 > img:hover {
  transform: scale(1.1);
  transition: 0.3s ease-in-out;
}
/* .ad2 img:hover{
  opacity: 1;
} */
@media (max-width:560px) {
  .ad>.ad_text{
    display: none;
  }
  .ad2{
    width: 100%;
    
  }
  .ad2>img{
    overflow: hidden;
    background-position: center;
  }
  .product_top p{
    font-size: 26px;
  }
  
}
</style>


